<template>
  <div class="fkts-reader">
    <fkts-sidebar></fkts-sidebar>
    <div :class="{ movedWithSidebar: isSidebarOpened }"
        @click="loadNextPage">
      <div class="w-100 imageWrapper">
          <div v-if="!isMultipageMode" class="singleImage" :class="{ fullpage: isFullPageMode }">
            <fkts-loading-image :src="`/manga/${selectedManga}/${selectedVolume}/${selectedChapter}/${selectedPage}.png`"></fkts-loading-image>
          </div>
          <div v-for="pageIndex in pages" class="singleImage" :key="pageIndex" v-else :class="{ fullpage: isFullPageMode }">
            <fkts-loading-image :src="`/manga/${selectedManga}/${selectedVolume}/${selectedChapter}/${pageIndex - 1}.png`"></fkts-loading-image>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
import fktsSidebar from './fkts-sidebar'
import fktsLoadingImage from './fkts-loading-image'
import store from '../store'

export default {
  name: 'fkts-reader',
  components: {
    fktsSidebar,
    fktsLoadingImage
  },
  data() {
    return {
      pages: 0,
      // baseURL: 'http://localhost:8080',
    }
  },
  created() {
    this.getAllPages()
  },
  computed: {
    uploadsJSON() {
      return store.getters.uploadsJSON()
    },
    isSidebarOpened() {
      return store.getters.isSidebarOpened()
    },
    selectedPage() {
      return store.getters.selectedPage()
    },
    selectedChapter() {
      return store.getters.selectedChapter()
    },
    selectedVolume() {
      return store.getters.selectedVolume()
    },
    selectedManga() {
      return store.getters.selectedManga()
    },
    isFullPageMode() {
      return store.getters.isFullPageMode()
    },
    isMultipageMode() {
      return store.getters.isMultipageMode()
    },
    chapters() {
      return store.getters.chapters()
    },
  },
  methods: {
    loadNextPage() {
      if (this.isMultipageMode) {
        this.$router.push('/')
        return
      }
      store.actions.setSelectedPage(this.selectedPage + 1)
        .then(() => this.$router.push(`/read/${this.selectedManga}/${this.selectedVolume}/${this.selectedChapter}/${this.selectedPage}`))
        .then(() => window.scrollTo(0, 0))
        .catch(() => this.$router.replace('/'))
    },
    getAllPages() {
      const chapter = this.uploadsJSON.manga[0]['shingeki-no-kyojin'].volumes[0].chapters[0]
      this.pages = chapter.pagesCount
      this.loadedImages = []
      for (let i = 0; i < this.pages; i += 1) {
        this.loadedImages.push(false)
      }
    }
  }
}
</script>
<style scoped>
.movedWithSidebar {
  margin-left: 250px;
}
.fkts-reader {
  min-height: 100vh;
  background: #303037;
}

.imageWrapper {
  cursor: pointer;
  padding-top: 120px;
}

.imageWrapper img {
  padding-top: 10px;
  padding-bottom: 10px;
}

.fullpage {
   height: 100vh;
}

.fullpage img {
  width: auto;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
}

.singleImage {
  text-align: center;
  line-height: 100vh;
}

@media screen and (min-width: 1025px) {
  .imageWrapper {
    cursor: pointer;
    padding-top: 0px;
  }
}
</style>