<template>
      <b-spinner v-if="!loaded" label="Spinning"></b-spinner>
      <b-img
        :src="imageSource"
        center
        fluid
        v-else
      >
      </b-img>
</template>
<script>
export default {
  name: 'fkts-loading-image',
  props: {
    src: String,
  },
  watch: {
    src() {
      this.imageSource = '',
      this.loaded = false
      this.loadImage()
    },
  },
  created() {
    this.imageSource = '',
    this.loaded = false
    this.loadImage()
  },
  data() {
    return {
      imageSource: '',
      loaded: false,
    }
  },
  methods: {
    loadImage() {
      const img = new Image()
      img.onload = () => {
        this.imageSource = img.src
        this.loaded = true
      }
      img.src = this.src
    }
  }
}
</script>
<style scoped>
.hidden {
  display: none;
}
</style>