<template>
  <fkts-layout>
    <div class="card-container rounded m-2 p-3">
      <div class="row no-gutters">
        <div class="col-md-4">
          <img src="volume31-cover.png" class="card-img" alt="..." />
        </div>
        <div class="col-md-8">
          <div class="card-body">
            <h1 class="card-title">Shingeki no Kyojin</h1>
            <p
              class="card-text"
            >"That day, the human race remembered the terror of being dominated by them, and the shame of being held captive in a birdcage..." — Over 100 years ago, a natural predator of humanity appeared: the Titans, giant humanoid but mindless monsters whose sole purpose of existence seemed to be to devour humans. There was an insurmountable gap in power between them and mankind, and as a result, humanity was rapidly exterminated to the brink of extinction. The survivors responded by constructing three concentric walls: Wall Maria, Wall Rose and Wall Sina, which graced them with a century of peace. However, one day a Colossal Titan far larger than any other seen before breached the outer wall, allowing the smaller Titans to invade the human territory and forcing the survivors to retreat to the inner walls. Eren Jaeger, a boy whose mother was eaten during the invasion, vowed to wipe every last Titan off the face of the Earth, and joined the military determined to exact his revenge.</p>
            <router-link
              class="btn btn-dark read-btn mb-2"
              :to="`/read/shingeki-no-kyojin/${latestVolume}/${latestChapter}/0`"
              role="button"
            >
              <h5>
                <b>READ THE LATEST CHAPTER</b>
              </h5>
              <b>Chapter {{latestChapter}}</b> "{{latestChapterName}}"
            </router-link>
            <p class="card-text">
              We are big fans of the series "Attack On Titan" and we appreciate working on it.
              <br />Please consider to keep supporting the mangaka by purchasing the official release when it’s available !
            </p>
          </div>
        </div>
      </div>
    </div>
  </fkts-layout>
</template>
<script>
import fktsLayout from './fkts-layout'
import store from '../store'

export default {
  name: 'home',
  components: {
    fktsLayout,
  },
  data() {
    return {
      snk: {},
      latestVolume: 0,
      latestChapter: 0,
      latestChapterName: '',
    }
  },
  created() {
    this.snk = store.getters.uploadsJSON().manga[0]['shingeki-no-kyojin']
    const volume = this.snk.volumes[this.snk.volumes.length - 1]
    this.latestVolume = volume.id
    const chapter = volume.chapters[volume.chapters.length - 1]
    this.latestChapter = chapter.id
    this.latestChapterName = chapter.name
  }
}
</script>
<style>
.card-container {
  background: rgba(19, 19, 19, 0.8);
}
.read-btn {
  background-color: #421222 !important;
  width: 100%;
}
.card-text {
  color: lightgray;
}
</style>