import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/fkts-homepage.vue'
import fktsReader from '../components/fkts-reader.vue'
import fktsNotFound from '../components/fkts-not-found.vue'
import store from '../store'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '*',
      name: 'not-found',
      component: fktsNotFound,
    },
    {
      path: '/read/:manga/:volume(\\d+)/:chapter(\\d+)/:page(\\d+)',
      name: 'reader',
      component: fktsReader,
      props: true,
    },
  ],
  mode: 'history'
})

router.beforeEach(async (to, _from, next) => {
  if (!store.getters.uploadsJSON().manga) store.actions.setUploadsJSON(await (await fetch('/upload.json')).json())
  if (to.name === 'reader') {
    if (!store.getters.uploadsJSON().manga[0][to.params.manga]) {
      next({ name: 'not-found', params: { '0': to.path }})
      return
    }
    store.actions.setSelectedManga(to.params.manga)
        .then(() =>  store.actions.setSelectedVolume(Number(to.params.volume)))
        .then(() => store.actions.setSelectedChapter(Number(to.params.chapter)))
        .then(() => store.actions.setSelectedPage(Number(to.params.page)))
        .catch(() => next('/'))
  }
  next()
})

export default router