import mutations from './mutations'
import state from './state'

const handlePageChange = (page) => {
  const chapter = state.uploadsJSON.manga[0]['shingeki-no-kyojin'].volumes[0].chapters[0]
  let newPage = page
  if (newPage >= chapter.pagesCount || newPage < 0) {
    throw new Error('Reached end of manga')
  }
  return newPage
}

const actions = {
  setUploadsJSON: json => new Promise((res) => {
    mutations.SET_UPLOADS_JSON(json)
    res()
  }),
  showReaderSidebar: () => new Promise((res) => {
    mutations.SET_SIDEBAR_STATE(true)
    res()
  }),
  hideReaderSidebar: () => new Promise((res) => {
    mutations.SET_SIDEBAR_STATE(false)
    res()
  }),
  setSelectedPage: page => new Promise((res) => {
    const newPage = handlePageChange(page)
    mutations.SET_SELECTED_PAGE(newPage)
    res()
  }),
  setSelectedChapter: chapter => new Promise((res) => {
    if (chapter !== state.uploadsJSON.manga[0]['shingeki-no-kyojin'].volumes[0].chapters[0].id) throw Error()
    mutations.SET_SELECTED_CHAPTER(chapter)
    res()
  }),
  setSelectedVolume: volume => new Promise((res) => {
    if (volume !== state.uploadsJSON.manga[0]['shingeki-no-kyojin'].volumes[0].id) throw Error()
    mutations.SET_SELECTED_VOLUME(volume)
    res()
  }),
  setSelectedManga: manga => new Promise((res) => {
    mutations.SET_SELECTED_MANGA(manga)
    res()
  }),
  setFullpageMode: isFullpage => new Promise((res) => {
    mutations.SET_FULLPAGE_MODE(isFullpage)
    res()
  }),
  setMultipageMode: isMultipage => new Promise((res) => {
    mutations.SET_MULTIPAGE_MODE(isMultipage)
    res()
  }),
}

export default actions